  color: #000000;
  text-decoration: none;
  font-size: 14px;
  
}
.nav-links ul li::after{
  content: '';
  width: 0%;
  height: 2px;
  background: #fa7e09;
  display: block;
  margin: auto;
}
.nav-links ul li:hover::after{
  width: 100%;
}
